.Field {
  height: 100%;
  width: 100%;
  min-height: 20px;
  border: 1px groove var(--border-color);
  color: var(--text-color);
  display: grid;
  -ms-flex-align: center;
      align-items: center;
  border-radius: 3px;
  -webkit-transition: all 0.5s ease-out;
  -o-transition: all 0.5s ease-out;
  transition: all 0.5s ease-out;
}

.Elevated {
  background-color: var(--field-color);  
}
.Status {
  position: fixed;
  right: 0;
  bottom: 0;
  width: 30%;
}



